<template>
  <h2 class="title">---------------------- value  ----------------------</h2>
  <div class="basic section">
    <h3>Hello, {{ title }}</h3>
    <h3>{{ title }}   .</h3>
    <p>{{ message }}</p>
    <p v-html="message"></p>
    <div>
      <!-- v-model  -->
      <input type="text" v-model="nickname" name="" id="">
    </div>
    <div>
      <input type="number" v-model="age">
    </div>
  </div>
  <div class="section">
    <textarea v-model="message" cols="30" rows="10"></textarea>
  </div>
  <div class="section">
    <select name="city" v-model="city">
      <option value="01"></option>
      <option value="02" selected></option>
      <option value="03"></option>
      <option value="04"></option>
    </select>
  </div>
  <div class="section">
    <label><input type="checkbox" v-model="cbox">{{ cbox }}</label>
    <label><input type="checkbox" v-model="cbox2" true-value="" false-value="">{{ cbox2 }}</label>
  </div>
  <div class="section">
    <p> ?</p>
    <label><input type="checkbox" v-model="goodfood" value=""></label>
    <label><input type="checkbox" v-model="goodfood" value=""></label>
    <label><input type="checkbox" v-model="goodfood" value=""></label>
    <p>   {{ goodfood }}.</p><br/>

    <p> ?</p>
    <label><input type="checkbox" v-model="badfood" value=""></label>
    <label><input type="checkbox" v-model="badfood" value=""></label>
    <label><input type="checkbox" v-model="badfood" value=""></label>

    <p>   {{ badfood }}.</p>
  </div>
  <div class="section">
    <p> ?</p>
    <label><input type="radio" v-model="gender" value=""></label>
    <label><input type="radio" v-model="gender" value=""></label>
    <p> {{ gender }} .</p>
  </div>
  <h2 class="title">----------------------   ----------------------</h2>
  <div class="section">
    <img v-bind:src="imgSrc" v-bind:title="tooltip"/>
  </div>
  <div class="section">
    <button v-bind:disabled="show1"></button>
    <button v-bind:disabled="show2"></button>
  </div>
  <div class="section">
    <button v-bind:style="btn1"></button>
    <button v-bind:style="btn2"></button>
  </div>
  <h2 class="title">----------------------  (v-for, v-if, v-else) ----------------------</h2>
  <div class="section">
    <table>
      <thead>
        <tr>
          <th style="border: 1px solid gray"></th>
          <th style="border: 1px solid gray"></th>
          <th style="border: 1px solid gray"></th>
          <th style="border: 1px solid gray"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-bind:key="index" v-for="(item, index) in products">
          <td style="border: 1px solid gray">{{ item.name }}</td>
          <td style="border: 1px solid gray">{{ item.price }}</td>
          <td style="border: 1px solid gray">{{ item.category }}</td>
          <td style="border: 1px solid gray">3{{ item.delivery }}000</td>
        </tr>
      </tbody>
    </table>
  </div>
  <div class="section">
    <!-- v-if    -->
    <p v-if="true">if</p>
    <p v-if="false">if</p>
    <!-- v-show  ,    -->
    <p v-show="true">show</p>
    <p v-show="false">show</p>
  </div>
  <h2 class="title">---------------------- (v-on / @) ----------------------</h2>
  <div class="section">
    <button v-on:click="increaseCounter">()</button>
    <button @:click="decreaseCounter">()</button>
    <p>counter: {{ counter }}</p>    
    <button @:click="increaseCounter(), showMsg()"> </button>
    <button @:click="decreaseCounter(), showMsg()"> </button>
    <br/>
    <input type="number" v-model="countValue">
    <button @:click="applyCounter"></button>
    <br/>
  </div>
  <div class="section">
    <select v-model="cityValue" @change="changeCity">
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
    </select>
  </div>
  <div class="section">
    <input type="text" v-model="emailValue" @input="changeEmail" placeholder=" ">
    <p v-if="errEmail">{{ errEmail }}</p>
    <!-- <p>{{ emailValue }} / {{ errEmail }}</p> -->
  </div>
  <div class="section">
    <input type="text" v-model="pwdValue1" @input="changePwd1" placeholder=" "><br/>
    <input type="text" v-model="pwdValue2" @input="changePwd1" placeholder="  ">
    <p v-if="errPwd">{{ errPwd }}</p>
  </div>
  <!--   computed  -->
  <div class="section">
    <p>{{ hello() }}</p>
    <p>{{ hello() }}</p>
    <p>{{ hello() }}</p>
    <br/>
    <p>{{ hello2 }}</p>
    <p>{{ hello2 }}</p>
    <p>{{ hello2 }}</p>
  </div>
  <div class="section">
    : <input type="text" v-model="lastName" @input="changeLastName"><br/>
    : <input type="text" v-model="firstName" @input="changeFirstName"><br/>
    <p>method: {{ methodFullName() }}</p>
    <p>method: {{ methodFullName() }}</p>
    <p>computed: {{ computedFullName }}</p>
    <p>computed: {{ computedFullName }}</p>
    <p>fullName: {{ fullName }}</p>
    <p>fullName: {{ fullName }}</p>
  </div>
  <!-- watch -->
  <div class="section">
    <h4>user_info : {{ userInfo }}</h4>
    <input type="text" v-model="userName"><br/>
    <input type="text" v-model="userAge">
  </div>
</template>

<script>
export default {
  name: 'BasicView',
  components: {
    
  },
  data() {
    return {
      title : 'IT',
      message : '<b>IT</b> <b><span style="color: red;">5</span></b>    .',
      nickname : '',
      age : 22+3,
      city : '04',
      cbox : true,
      cbox2 : '',
      goodfood : [],
      badfood : [],
      gender : '',
      imgSrc : 'https://borgssam.github.io/MySite/img/album_01.jpg',
      tooltip : '',
      show1 : true,
      show2 : false,
      btn1 : {
        backgroundColor : '#49cc6c',
        color : '#0f6326',
        fontSize: '30px',
        border: 'none',
        borderRadius: '6px',
        padding: '8px 16px',
      },
      btn2 : {
        backgroundColor: '#8249cc',
        color : '#3a1766',
        fontSize: '30px',
        border: 'none',
        borderRadius: '6px',
        padding: '8px 16px',
      },
      products : [
        { "name" : "1", "price" : "2501", "category" : "PC1", "delivery" : "1000"},
        { "name" : "2", "price" : "2502", "category" : "PC2", "delivery" : "2000"},
        { "name" : "3", "price" : "2503", "category" : "PC3", "delivery" : "3000"},
      ],
      counter : 0,
      countValue : 10,
      cityValue : '',
      emailValue : '',
      errEmail : '',
      pwdValue1: '',
      pwdValue2: '',
      errPwd : ' ',
      lastName : '',
      firstName : '',
      fullNamae : '',
      userName : '',
      userAge : '30',
      userInfo : '',
    };
  },
  watch : {
    userName() {
      this.userInfo = this.userName + '(' + this.userAge + ')';
    },
    userAge() {
      this.userInfo = this.userName + '(' + this.userAge + ')';
    }
  },
  setup() {
    
  },
  created() {
    
  },
  mounted() {
    
  },
  unmounted() {
    
  },
  computed : {
    hello2() {
      // 1 
      // console.log('hello2 ');
      return ', .';
    },
    computedFullName() {
      console.log('computeFullName ');
      return this.lastName + this.firstName;
    }
  },
  methods: {
    increaseCounter(){
      this.counter = this.counter + 1;
    },
    decreaseCounter() {
      this.counter = this.counter - 1;
    },
    applyCounter() {
      this.counter = this.countValue;
    },
    showMsg() {
      alert('  => ' + this.counter);
    },
    changeCity() {
      alert(' : ' + this.cityValue);
    },
    changeEmail() {
      this.emailValue
      //    
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if(this.emailValue === '' || emailPattern.test(this.emailValue)) {
        this.errEmail = '';
      } else {
        this.errEmail = '  .'
      }
    },
    changePwd1() {
      // if(this.pwdValue1 === pwdPattern.number(this))
      
      // if(this.pwdValue1 < pwdPattern && this.pwdValue2 < pwdPattern) {
      //   this.errPwd = '8  .'
      // } else {
      //   this.errPwd = '8   .'
      // }

      if(this.pwdValue1 === '') {
        this.errPwd = ' ';
      } else if(this.pwdValue1.length > 7) {
        this.errPwd = '8   ';
        this.pwdValue1 = '';
      } else if(this.pwdValue2 === '') {
        this.errPwd = ' ';
      } else if(this.pwdValue1 === this.pwdValue2) {
        this.errPwd = ' .';
      } else {
        this.errPwd = '  ';
      }
    },
    hello() {
      //  
      // console.log('hello() ');
      return ', .';
    },
    changeLastName() {
      // lastName   fullName 
      console.log('changeLastName ');
      this.fullName = this.lastName + this.firstName;
    },
    changeFirstName() {
      // firstName   fullName 
      console.log('changeFirstName ');
      this.fullName = this.lastName + this.firstName;
    },
    methodFullName() {
      //  fullName 
      console.log('methodFullName ');
      return this.lastName + this.firstName;
    },
  }
};
</script>

<style scoped>
  table {
    border-collapse: collapse;
    width: 90%;
    margin: 0 auto;
  }
  td, th {
    border: 1px solid #ddd;
    text-align: left;
    padding: 8px;
  }
  th {
    text-align: center;
    font-weight: 600;
  }
  h2.title {
    display: block;
    color: #f52563;
    margin: 100px 0 30px;
  }
  div.section {
    width: 80%;
    margin: 20px auto;
    border: 1px solid #ddd;
    padding: 20px;
  }
</style>