<template>
  <div class="section">
    <h2 class="title">data()?</h2>
    <p>      .</p>
    <p>   data()  data()      .</p>
    <p> script data()  . ..</p>
    <br/>
    <p>* : <a href="https://v3-docs.vuejs-korea.org/api/options-state.html#data">https://v3-docs.vuejs-korea.org/api/options-state.html#data</a></p>
  </div>
  <div class="section">
    <h2 class="title">p  </h2>
    <p> IT <b></b> </p>
    
    <div class="line"></div>
    
    <h2 class="title">p mustache(;  2 `{ { data } }`) </h2>
    <p> {{ title }} </p>
    
    <div class="line"></div>
    
    <h2 class="title">p v-text </h2>
    <p v-text="title"></p>
    
    <div class="line"></div>
    
    <h2 class="title">p v-html </h2>
    <p v-html="title"></p>
  </div>
  <div class="section">
    <h2 class="title">input value </h2>
    <input type="text" name="" value="333" /><br/>

    <div class="line"></div>

    <!-- input v-model . -->
    <h2 class="title">input v-model </h2>
    <!-- v-model        .-->
    <!--  onChange . -->
    <input type="text" v-model="nick" /><br/> 
    <input type="number" v-model="age" /><br/>
  </div>
  <div class="section">
    <h2 class="title">textarea ( )</h2>
    <textarea cols="30" rows="10"> </textarea><br/>

    <div class="line"></div>
    
    <!-- textarea v-model . -->
    <h2 class="title">textarea v-model</h2>
    <textarea v-model="message" cols="30" rows="10"></textarea>
  </div>
  <div class="section">
    <h2 class="title">select > option   </h2>
    <select name="" id="">
      <option value="01"></option>
      <option value="02"></option>
      <option value="03"></option>
      <option value="04"></option>
    </select>
    
    <div class="line"></div>

    <h2 class="title">select v-model </h2>
    <!-- select  v-model . -->
    <select name="" id="" v-model="city">
      <option value="01"></option>
      <option value="02"></option>
      <option value="03"></option>
      <option value="04"></option>
    </select>
  </div>

  <div class="section">
    <!-- input id label for     checkbox check .   checkbox  check . -->
    <h2 class="title">  </h2>
    <input type="checkbox" id="cbtn_agree">
    <label for="cbtn_agree"></label><br/>
    
    <div class="line"></div>

    <h2 class="title">label  input:checkbox   v-model </h2>
    <label><input type="checkbox" v-model="agree"></label>
    
    <div class="line"></div>

    <!-- true-value false-value   checkbox   true, false .  true-value false-value  value true  value false        . -->
    <h2 class="title">label  input:checkbox   v-model <br/> true-value false-value .</h2>
    <label><input type="checkbox" v-model="agree2"
      true-value="" false-value=""  
    >{{ agree2 }}</label>

    <div class="line"></div>

    <h2 class="title">label > input ([ ])    v-model </h2>
    <p>   </p><br/>
    <label><input type="checkbox" v-model="pizza" value="">  </label><br/>
    <label><input type="checkbox" v-model="pizza" value="">  </label><br/>
    <label><input type="checkbox" v-model="pizza" value="">  </label><br/><br/>
    <p>    {{ pizza }} .</p>

    <div class="line"></div>
    <h2 class="title">label > input v-model .<br/> ([ ])   ' '     .</h2>
    <p> ?</p><br/>
    <label>
      <input type="radio" v-model="sweet_sour_pork" value="">
    </label>
    <label>
      <input type="radio" v-model="sweet_sour_pork" value="">
    </label>
    <br/><br/>
    <p> <span style="color: red; font-weight: 800;">{{ sweet_sour_pork }}</span> .</p>
  </div>

  <div class="section">
    <h2 class="title"> . img  </h2>
    <img src="https://borgssam.github.io/MySite/img/album_01.jpg" alt=""><br/>
    <div class="line"></div>
    <h2 class="title"> . img v-bind:src , v-bind:title (tooltip)</h2>
    <img v-bind:src="imgSrc" alt="" v-bind:title="imgTitle"><br/>

    <div class="line"></div>

    <h2 class="title">button ":disabled   true/false ", ":style   data()   "</h2>
    <div>
      <button :disabled="btn_01" :style="color_red"></button>
      <button :disabled="btn_02" :style="color_blue"></button>
    </div>
  </div>

  <div class="section">
    <!-- v-for -->
    <h2 class="title">table  </h2>
    <table>
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td></td>
          <td>3000</td>
          <td>pc</td>
          <td>25</td>
        </tr>
        <tr>
          <td></td>
          <td>3000</td>
          <td>pc</td>
          <td>25</td>
        </tr>
        <tr>
          <td></td>
          <td>3000</td>
          <td>pc</td>
          <td>25</td>
        </tr>
      </tbody>
    </table>

    <div class="line"></div>

    <h2 class="title">table v-for, v-bind:key </h2>
    <table>
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr v-for="(item, index) in goods" v-bind:key="index">
          <td>{{ item.name }}</td>
          <td>{{ item.price }}</td>
          <td>{{ item.category }}</td>
          <td>{{ item.delivery }}</td>
        </tr>
      </tbody>
    </table>

    <div class="line"></div>

    <!-- v-if v-else-if v-else -->
    <h2>v-if   true  , false   .</h2>
    <p v-if="true"></p>
    <p v-if="false"></p>

    <div class="line"></div>

    <div>
      <h2 class="title">v-if, v-else-if, v-else</h2>
      <label>: <input type="number" v-model="score"></label>
      <br/>
      <p v-if="score >= 90">A</p>
      <p v-else-if="score >= 80">B</p>
      <p v-else-if="score >= 70">C</p>
      <p v-else-if="score >= 60">D</p>
      <p v-else>F</p>
    </div>

    <div class="line"></div>

    <div>
      <h2 class="title">.. watch  ..</h2>
      <label>: <input type="number" v-model="kor" placeholder="  ."></label><br/>
      <label>: <input type="number" v-model="eng" placeholder="  ."></label><br/>
      <label>: <input type="number" v-model="math" placeholder="  ."></label>
      <br/><br/>
      <p> ? <span style="color:blue; font-weight:bold;">{{ korTxt }}</span></p>
      <p> ? <span style="color:blue; font-weight:bold;">{{ engTxt }}</span></p>
      <p> ? <span style="color:blue; font-weight:bold;">{{ mathTxt }}</span></p>
    </div>

    <div class="line"></div>

    <h2 class="title"> v-on // @ </h2>
    <div>
      <label>: <input type="number" v-model="kor" placeholder="  ."></label><br/>
      <label>: <input type="number" v-model="eng" placeholder="  ."></label><br/>
      <label>: <input type="number" v-model="math" placeholder="  ."></label>
      <br/><br/>
      <p v-if="avg >= 90">A({{ avg }})</p>
      <p v-else-if="avg >= 80">B({{ avg }})</p>
      <p v-else-if="avg >= 70">C({{ avg }})</p>
      <p v-else-if="avg >= 60">D({{ avg }})</p>
      <p v-else>D({{ avg }})</p>
      <button v-on:click="showScore"></button>
    </div>

    <div class="line"></div>

    <div>
      <h2 class="title">..</h2>
      <input type="text" v-model="dan" placeholder=" " /> 
      <button v-on:click="multiplicationTable"></button>
      <p>{{ dan }}</p>
      <label v-for="(item, index) in rseultDanTxt" v-bind:key="index" >
        <p>{{ item.dan }} * {{ item.dan2 }} = {{ item.resultDan }}</p>
      </label>
    </div>
  </div>
  <div class="section">
    <h2 class="title">select > option  </h2>
    <select name="" id="">
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value="" selected></option>
    </select>

    <div class="line"></div>

    <h2 class="title">select > option vue <br/>select v-modiel, @change </h2>
    <select v-model="city2" @change="changeCity">
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
    </select>
    <p>{{ message2 }}</p>
  </div>

  <div class="section">
    <h2 class="title">    </h2>
    <p>
      <label>
        <input type="text" placeholder=" " v-model="emailValue" @input="changeEmail">
      </label><br/>
      <span> => {{ msgEmail }}</span>
    </p>
    <p>
      <label>
        <input type="text" placeholder=" " v-model="pwdValue" @input="changePwd">
      </label><br/>
      <span> => {{ msgPwd }}</span>
    </p>
    <p>
      <label>
        <input type="text" v-model="pwdValue2" placeholder=" " @input="changePwd2">
      </label><br/>
      <span> => {{ msgPwd2 }}</span>
    </p>
  </div>

  <div class="section">
    <h2 class="title">method  </h2>
    <div>
      <label>  : <input type="text" v-model="familyName1" @input="makeFullName1"> </label><br/>
      <label>  : <input type="text" v-model="name1" @input="makeFullName1"> </label>
      <p>(method) : {{ fullName1 }}</p>
    </div>

    <div class="line"></div>

    <h2 class="title">computed  </h2>
    <div>
      <label>  : <input type="text" v-model="familyName2"></label><br/>
      <label>  : <input type="text" v-model="name2"> </label>
      <p>(computed) : {{ makeFullName2 }}</p>
    </div>

    <div class="line"></div>

    <h2 class="title">watch  </h2>
    <div>
      <label>  : <input type="text" v-model="familyName3" @input="makeFullName3"> </label><br/>
      <label>  : <input type="text" v-model="name3"> </label>
      <p>(watch) : {{ fullName3 }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TestViewCopy',
  components: {}, //   
  data() {
    return {
      title: ' IT  ',
      nick: ' ',
      age: 30,
      message: '    ~',
      city: '04',
      agree: false,
      agree2: '',
      pizza: [],
      sweet_sour_pork: '',
      imgSrc: 'https://borgssam.github.io/MySite/img/album_01.jpg',
      imgTitle: ' ',
      btn_01: true,
      btn_02: false,
      color_red: { color: 'red', fontSize: '24px' },
      color_blue: { color: 'blue', fontSize:'24px' },
      goods: [
        {"name":"", "price":10000, "category":"PC", "delivery":2500},
        {"name":"", "price":30000, "category":"PC", "delivery":2500},
        {"name":"()", "price":2000000, "category":"PC", "delivery":0},
      ],
      score: '',
      kor: 0,
      korTxt: '',
      eng: 0,
      engTxt: '',
      math: 0,
      mathTxt: '',
      avg: 0,
      dan: 0,
      rseultDanTxt: [],
      city2: '',
      message2: ' ',
      emailValue: '',
      msgEmail: ' ',
      pwdValue: '',
      pwdValue2: '',
      msgPwd: ' ',
      msgPwd2: '  ',
      familyName1:'',
      name1:'',
      fullName1:'',
      familyName2:'',
      name2:'',
      familyName3:'',
      name3:'',
      fullName3:'',
      
    }  
  },
  watch : {
    kor() {
      if (this.kor >= 90 ) {
        this.korTxt = "A";
      } else if (this.kor >= 80) {
        this.korTxt = "B";
      } else if (this.kor >= 70) {
        this.korTxt = "C";
      } else if (this.kor >= 60) {
        this.korTxt = "D";
      } else {
        this.korTxt = "F";
      }

      return this.korTxt;
    },
    eng() {
      if (this.eng >= 90 ) {
        this.engTxt = "A";
      } else if (this.eng >= 80) {
        this.engTxt = "B";
      } else if (this.eng >= 70) {
        this.engTxt = "C";
      } else if (this.eng >= 60) {
        this.engTxt = "D";
      } else {
        this.engTxt = "F";
      }

      return this.engTxt;
    },
    math() {
      if (this.math >= 90 ) {
        this.mathTxt = "A";
      } else if (this.math >= 80) {
        this.mathTxt = "B";
      } else if (this.math >= 70) {
        this.mathTxt = "C";
      } else if (this.math >= 60) {
        this.mathTxt = "D";
      } else {
        this.mathTxt = "F";
      }

      return this.mathTxt;
    },
    familyName3(){
      this.fullName3 = this.familyName3+ ' - ' + this.name3;
    },
    name3(){
      this.fullName3 = this.familyName3+ ' - ' + this.name3;
    }
  },
  methods: {
    showMessage() {
      alert(' ');
    },
    showScore() {
      this.avg = (this.kor + this.eng + this.math) / 3;
    },
    multiplicationTable() {
      this.rseultDanTxt = []; // 
      for(let dan2 = 1; dan2 < 10; dan2++){
        this.rseultDanTxt.push({
          dan: this.dan,
          dan2: dan2,
          resultDan: this.dan * dan2,
        })
      }  
    },
    changeCity() {
      switch(this.city) {
        case '':
            this.message= '. .';
            break;
        case '':
            this.message= '. .';
            break;
        case '':
            this.message= '. .';
            break;
        case '':
            this.message= '. .';
            break;
        case '':
            this.message= '. .';
            break;
        default:
            break;
      }
    },
    changeEmail() {
      // this.emailValue    
      //  + @ +  + . + 
      const emailExp = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

      if(this.emailValue.length == 0){
        this.msgEmail = ' ';
      } else if (emailExp.test(this.emailValue) == true) {
        this.msgEmail = '  .';
      } else {
        this.msgEmail = '   .';
      }
    },
    changePwd() {
      let pwdExp1 = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,15}$/;
      let pwdExp2 = /^(?=.*[a-zA-Z])(?=.*[0-9]).{8,15}$/;
      let pwdExp3 = /^(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,15}$/;
      let pwdExp4 = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-]).{8,15}$/;

      if(this.pwdValue.length == 0){
        this.msgPwd = ' ';
      } else if (this.pwdValue.length > 12 ) {
        this.msgPwd = '12   .';
      } else if (pwdExp1.test(this.pwdValue)) {
        this.msgPwd = 'GOOOOOOOD~';
      } else if (pwdExp2.test(this.pwdValue) == true ) {
        this.msgPwd = '   ';
      } else if (pwdExp3.test(this.pwdValue) == true) {
        this.msgPwd = '   ';
      } else if (pwdExp4.test(this.pwdValue) == true) {
        this.msgPwd = '   .';
      } else {
        this.msgPwd = ' 8 , 15  , , ,    .'
      }
    },
    changePwd2() {
      if(this.pwdValue2.length == 0){
        this.msgPwd2 = '  ';
      } else if (this.pwdValue2 == this.pwdValue) {
        this.msgPwd2 = ' .';
      } else {
        this.msgPwd2 = '  .'
      }
    },
    makeFullName1(){
      this.fullName1 = this.familyName1+ ' - ' + this.name1;
    }
  },
  computed: {
    makeFullName2(){
      return this.familyName2+ ' - ' + this.name2;
    }
  }
}
</script>

<!-- scoped       -->
<style scoped> 
  h2.title {
    display: block;
    font-size: 20px;
    color: #aa3c89;
    margin: 20px auto;
  }
  div.section {
    width: 80%;
    margin: 20px auto;
    border: 1px solid #ddd;
    padding: 20px;
  }
  div.line {
    width: 100%;
    height: 1px;
    border: 1px dashed rgb(163, 163, 163);
    margin: 30px auto;
  }


  table {
    width: 100%;
    border-collapse: collapse;
  }
  td, th {
    border: 1px solid #ddd;
  }
</style>