<template>
  <div class="son">
    <p>  : {{ message }} {{ msg }}</p>
    <cute-g-son11 ref="gson11" />
    <cute-g-son12 ref="gson12" @event_report="receiveEvent"/>
    <input type="text" v-model="sndMessage">
    <button @click="clickSon10_1">1</button>
    <button @click="clickSon10_2">2</button>
    <button @click="clickSon10">1,2</button>
    <button @click="clickSon10_3">3</button>
  </div>
</template>

<script>
import CuteGSon11 from './CuteGSon11.vue';
import CuteGSon12 from './CuteGSon12.vue';
export default {
  name: 'GoodSon10',
  props: {
    
  },
  components: {
    CuteGSon11,
    CuteGSon12,
  },
  data() {
    return {
      message : '',
      sndMessage : '',
    };
  },
  watch: {
    // sample1() {
    //   console.log('');
    // }
    //     .
  },
  computed: {

  },
  methods: {
    clickSon10_1() {
      this.message = '   .';
      this.$refs.gson11.clickMsg();
    },
    clickSon10_2() {
      this.message = '   .'
      this.$refs.gson12.clickMsg('  ');
    },
    clickSon10() {
      this.message = ' 1, 2  .'
      this.$refs.gson11.clickMsg();
      this.$refs.gson12.clickMsg();
    },
    changeTextColor(idx, data) {
      this.$refs.gson11.changeTextColor(idx, data);
      this.$refs.gson12.changeTextColor(idx, data);
    },
    clickSon10_3() {
      this. message = '  '
      this.$refs.gson11.message = this.sndMessage;
    },
    receiveEvent(data) {
      this.message=(data);
    }
  },
  setup() {
    // Vue 3 Composition API setup      .
  },
  created() {
    //      .
  },
  mounted() {
    //  DOM     .
  },
  unmounted() {
    //      .
  }
};
</script>

<style scoped>
.son {
  background-color: moccasin;
  padding: 20px;
  margin: 20px;
}
.gson {
  background-color: rgb(137, 165, 202);
  margin: 20px;
}
.son button {
  padding: 6px;
  margin: 4px;
}
